.body {
  margin: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  position: relative;
}

.person__card {
  transform-origin: 50% 115px;
  padding: 10px 10px 16px;
  transition: transform 1s;
}

.person:hover .person__card {
  background-color: #eee;
  border-radius: 12px;
  position: absolute;
  transform: scale(1.2);
}

.person__photo {
  position: relative;
}

.person__img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: border-radius 1s;
  display: block;
}

.person:hover .person__img {
  border-radius: 60px;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
  position: absolute;
  top: 84px;
  left: 158px;
  right: 10px;
}

.person__name {
  text-align: center;
  color: #2f4f4f;
  margin-top: 8px;
  font-size: 20px;
  font-weight: 700;
  display: block;
}

.person__extra {
  color: #0000;
  text-align: center;
  margin-top: 9px;
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
  display: none;
}

.person:hover .person__extra {
  color: #000;
  display: block;
}

.person__sport:after {
  content: ",";
}

.person__pay:before {
  content: "$";
}

.person__pay:after {
  content: "M";
}
